.App {
  text-align: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: -1;
}

.top{
  margin-top:50px;
}

.description {
  color: white;
  font-size: 1.3em;
}

.custom-upload, .download {
    border-radius: 10px;
    display: inline-block;
    padding: 6px 12px;
    cursor: pointer;
    background: -webkit-gradient(linear, left top, right top, from(#007991), to(#00bc83));
    background: -webkit-linear-gradient(left, #007991, #00bc83);
    background: -o-linear-gradient(left, #007991, #00bc83);
    background: linear-gradient(90deg, #007991, #00bc83);     
    color: white;
    font-size: 2.5em;
    min-width: 25%;  
}

.custom-upload {  
    margin-right: 30px;
}

.custom-upload:hover, .download:hover {
  background: -webkit-gradient(linear, left top, right top, from(#0087a2), to(#00cd8f));
  background: -webkit-linear-gradient(left, #0087a2, #00cd8f);
  background: -o-linear-gradient(left, #0087a2, #00cd8f);
  background: linear-gradient(90deg, #0087a2, #00cd8f);
}

.custom-upload:active, .download:active {
  background: -webkit-gradient(linear, left top, right top, from(#006b80), to(#00ab77));
  background: -webkit-linear-gradient(left, #006b80, #00ab77);
  background: -o-linear-gradient(left, #006b80, #00ab77);
  background: linear-gradient(90deg, #006b80, #00ab77);
}

#upload {
  display: none;
}

.download {
  display: inline-block;
  text-decoration: none;
}

.error {
  color: #d9534f;
  margin-top: 30px;
  font-weight: bold;
  font-size: 1.5em;
}

.loading {
  color: white;
  margin-top: 30px;
  font-size: 1.3em;
}

.canvas {
  margin: 30px auto 0;
}

@media only screen and (max-width: 800px) {
  #canvas {
    width: 550px;
  }
}

@media only screen and (max-width: 558px) {
  #canvas {
    width: 380px;
  }
  .custom-upload {  
    margin-right: 0;
}

}

@media only screen and (max-width: 400px) {
  #canvas {
    width: 300px;
  }
}






